@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@300;400&display=swap);
.appBox {
  margin: 3vh;
}
.headFlex {
  display: flex;
  justify-content: space-between;
}
.square {
  height: 5vh;
  width: 5vw;
}
.companyName {
  margin-left: 1vw;
}
.informationFlex {
  display: flex;
}
.information {
  margin-left: 20px;
}

.unicode {
  color: rgb(150, 150, 150);
  cursor: pointer;
}

.gotoBtn {
  border: none;
  background-color: white;
  cursor: pointer;
}

Body {
  margin: 0;
  padding: 0;
}
.head {
  background-color: blueviolet;
  color: white;
  width: 100%;
  padding: 10px;
  height: 7vh;
}

.headinfo {
  display: flex;
  padding: 1rem;
}

.appinfo {
  margin-left: 3vw;
}

.main {
  display: flex;
  justify-content: space-evenly;
}

.appsHeading {
  padding-top: 10vh;
  padding-left: 5vw;
}

.appsContainer {
  display: flex;
  flex-direction: column;
}

.appsHeading {
  margin-bottom: 1vh;
}

.appsContent {
  display: flex;
  flex-direction: column;
  width: 45vw;
  margin-top: 5vh;
  margin-left: 5vh;
  overflow-y: scroll;
  max-height: 71vh;
}

.appsContentoff {
  background-color: rgb(241, 241, 241);
}

body {
  margin: 0;
  padding: 0;
  /* font-family: "Noto Sans JP", sans-serif; */
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-weight: 300;
}

.homeGrid {
  display: grid;
  height: 100vh;
  width: 100vw;
  grid-template-rows: 1fr 1fr;
  grid-template-columns: 1fr 1fr;
}

.mainImage {
  grid-row-start: 1;
  background-color: rgb(132, 65, 240);
  color: white;
}

.mainImageHeading {
  margin-left: 2rem;
}

.asset1 {
  height: 20vh;
  width: 30vw;
  left: 0;
  right: 0;
  margin: auto;
}

.bottomDescription {
  grid-row-start: 2;
  background-color: rgb(86, 30, 175);
  color: white;
  display: grid;
}

.bottomDescriptionHeading {
  padding-left: 3rem;
}

.apiData {
  grid-row-start: 1;
  grid-row-end: -1;
}

.iconGrid {
  display: grid;
  grid-row-gap: 4vh;
  grid-template-columns: 1fr 1fr;
  padding: 3rem;
  margin-top: 1vh;
}

